import { Col, Drawer, Form, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../helper/function";
import { MailAppService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import AssignTemplateComponent from "./AssignTemplateComponent";

export default function ManageMailAppModifyComponent(props: any) {
    const { setIsLoading } = props;
    const [name, setName] = useState('');
    const [comment, setComment] = useState('');
    const [keySecret, setKeySecret] = useState('');
    const [status, setStatus] = useState(0);

    const [visible, setVisible] = useState(false);
    const options = [
        {
            key: "active",
            label: "Đang hoạt động",
            value: 1
        },
        {
            key: "inactive",
            label: "Không hoạt động",
            value: 0
        }
    ]
    const loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            setIsLoading(true);
            MailAppService.GetDetail(props.keyData, (data: any) => {
                var row = data.data.data;
                setName(row.name);
                setComment(row.comment);
                setKeySecret(row.secret);
                setStatus(row.status);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.keyData, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function onClose() {
        props.setVisible(false);
        if (props.keyData !== undefined) {
            props.setKeData(undefined);
        }
    }

    function handleSubmitForm() {
        props.setIsLoading(true);
        var body = {
            name: name,
            comment: comment,
            status: status
        };

        if (props.keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        MailAppService.Add(body, (data: any) => {
            props.loadData();
            Fn.PrintMsgSuccess(data);
        }, () => {
            props.setIsLoading(false);
        })
    }

    function Edit(body: any) {
        MailAppService.GetDetail(props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            MailAppService.Edit(props.keyData, body, (data1: any) => {
                props.loadData();
                Fn.PrintMsgSuccess(data1);
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function handleAssignTemplate() {
        setVisible(true);
    }

    function handleDelete() {
        Swal.fire({
            title: "Xác nhận xóa!",
            text: "Bạn có muốn xóa app không?",
        }).then((confirm) => {
            if (confirm.isConfirmed) {
                DeleteApp();
            }
        })
    }

    function DeleteApp() {
        props.setIsLoading(true);
        MailAppService.GetDetail(props.keyData, (data: any) => {
            MailAppService.Delete(props.keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                onClose();
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            title={props.keyData === undefined ? "Thêm app mới" : "Cập nhật thông tin app"}
            open={props.visible}
            onClose={onClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên App"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Input
                    label="Chú thích"
                    value={comment}
                    setValue={setComment} />

                <MyFormItem.Input
                    label="Mã bí mật"
                    readOnly={true}
                    value={keySecret}
                    setValue={setKeySecret} />

                <MyFormItem.Select
                    label="Trạng thái"
                    value={status}
                    setValue={setStatus}
                    options={options} />

                <Row>
                    <Col span={24}>
                        <MyButton style={{ marginBottom: "10px" }}
                            type="primary"
                            value="Chọn template"
                            onClick={handleAssignTemplate} />
                    </Col>
                </Row>

                <MyButton
                    type="success"
                    isLoading={props.isLoading}
                    htmlType="submit"
                    value={props.keyData === undefined ? "Thêm" : "Cập nhật"} />

                <MyButton
                    type="danger"
                    isLoading={props.isLoading}
                    onClick={handleDelete}
                    value="Xóa" />
            </Form >

            <AssignTemplateComponent
                keyData={props.keyData}
                visible={visible}
                setVisible={setVisible}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading} />
        </Drawer >
    );
}