import { Drawer } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { MailAppService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";

class AssignTemplate {
    key: string | undefined;
    code: string | undefined;
    check: boolean | undefined;
}

export default function AssignTemplateComponent(props: any) {
    const { setIsLoading } = props;
    const [data, setData] = useState<AssignTemplate[]>([]);

    const loadData = useCallback(() => {
        if (props.visible) {
            setIsLoading(true);
            MailAppService.GetTemplate(props.keyData, (data: any) => {
                setData(data.data.data);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.visible, props.keyData, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleCloseDrawer() {
        props.setVisible(false);
    }

    function handleChangeSwitch(index: number, check: any) {
        let row = data;
        row[index].check = check;
        setData([...row]);
    }

    function RenderData() {
        return data.map((data: any, index: number) => {
            return (
                <div key={index}>
                    <MyFormItem.Switch value={data.check} label={data.code} onChange={(value: any) => {
                        handleChangeSwitch(index, value);
                    }} />
                </div>
            )
        })
    }

    function handleSaveAssign() {
        var body = data;
        props.setIsLoading(true);
        MailAppService.SaveTemplate(props.keyData, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            props.setIsLoading(false);
        });
    }

    return (
        <Drawer
            title="Chọn template"
            open={props.visible}
            onClose={handleCloseDrawer}>
            {RenderData()}
            <MyButton onClick={handleSaveAssign} value="Lưu" />
        </Drawer>
    )
}